<template>
    <main>
        <div class="container-fluid">
            <!-- START: Breadcrumbs-->
            <div class="d-block header-section mt-2 mb-3">
                <div>
                    <h1>
                        <b>Change SKU/Product Name</b>
                    </h1>
                </div>
            </div>
            <div class="card mb-3">
                <div class="row pb-3 pt-3 ml-0 mr-0">
                    <div class="row col-md-4">
                        <label class="col-md-3 pt-2">Product</label>
                        <select class="form-control col-md-9"
                            @change="onProductChange()"
                            v-model="selectedProductType"
                            name="Product"
                            :disabled="items.coilListing.length > 0"
                        >
                            <option disabled selected value="0">-select-</option>
                            <option
                                v-for="item in productTypeList"
                                :value="{ id: item.productId, text: item.productName }"
                                :key="item.productId"
                            >
                                {{ item.productName }}
                            </option>
                        </select>
                    </div>
                    <div class="row col-md-3">
                        <label class="col-md-5 pt-2">Package</label>
                        <select 
                            class="form-control col-md-7" 
                            @change="onPackageChange()" 
                            v-model="selectedPackage" 
                            name="Package"
                            :disabled="!selectedProductType"
                            >
                            <option disabled selected value="0">-select-</option>
                            <option
                                v-for="item in packageList"
                                :value="{ id: item.productId, text: item.productName }"
                                :key="item.productId"
                            >
                                {{ item.productName }}
                            </option>
                        </select>
                    </div>
                    <div class="row col-md-4">
                        <label class="col-md-3 pt-2" for="codeValue">Code</label>
                        <input
                            class="form-control col-md-9"
                            id="codeValue"
                            v-model="codeValue"
                            :disabled="!selectedProductType || !selectedPackage || showCartonSelection"
                        />
                    </div>
                    <div class="row col-md-2">
                        <button class="ml-3 btn btn-primary" :disabled="!codeValue" @click="fetchCoils()">Add</button>
                        <button class="ml-3 btn btn-primary" @click="resetCoils()">Reset</button>
                    </div>
                </div>
                <b-card v-if="showCartonSelection">
                    <h6>Please select the real codes from the Carton</h6>
                    <div>
                        <b-form-group class="mb-0">
                            <template v-slot:label>
                                <b-form-checkbox
                                v-model="selectAll"
                                :indeterminate="indeterminate"
                                aria-describedby="codes"
                                aria-controls="codes"
                                @change="toggleAll"
                                >
                                    {{ selectAll ? 'Deselect All' : 'Select All' }}
                                </b-form-checkbox>
                            </template>
                            <b-form-checkbox-group
                                id="codes"
                                v-model="selectedCodes"
                                :options="codes"
                                name="codes"
                                class="mb-3"
                            ></b-form-checkbox-group>
                        </b-form-group>
                    </div>
                    <div class="d-flex flex-row-reverse">
                        <b-button class="pull-right ml-3" size="sm" variant="primary" @click="cartonSelection()">ADD</b-button>
                        <b-button class="pull-right" size="sm" variant="primary" @click="showCartonSelection = false">CANCEL</b-button>
                    </div>
                </b-card>
                <b-card v-if="this.items.coilListing.length > 0">
                    <table id="tab" class="table itemListingFulfill coilListing">
                        <thead>
                            <tr>
                                <th>Code</th>
                                <th>Product Name</th>
                                <th>Product SKU</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item, index) in this.items.coilListing" :key="index">
                                <td>{{ item.code }}</td>
                                <td>{{ item.productName }}</td>
                                <td>{{ item.sku }}</td>
                                <td class="remove-x-btn text-center"><span @click="removeItem(index)"><b>X</b></span></td>
                            </tr>
                        </tbody>
                    </table>
                </b-card>
            </div>
            <div class="card mb-3 pb-3">
                <div class="col-md-12">
                    <p class="mt-2">Comments</p>
                    <div>
                        <textarea class="form-control" rows="5" v-model="comments"></textarea>
                    </div>
                </div>
            </div>
            <div class="card pb-3">
                <template>
                    <div class="col-md-12 pt-2 pb-2">
                        <b-form-group class="change-item-type" label="Do you want to ?">
                        <b-form-radio-group
                            id="radio-slots"
                            v-model="selectedChange"
                            name="radio-options-slots"
                            @change="typeChange"
                        >
                            <b-form-radio value="1">Change SKU</b-form-radio>
                            <b-form-radio value="2">Change Product Name</b-form-radio>
                            <b-form-radio value="3">Both</b-form-radio>
                        </b-form-radio-group>
                        </b-form-group>
                    </div>
                </template>
                <div class="row">
                    <div class="col-md-4">
                        <p class="mt-1 col-md-12">SKU</p>
                        <div class="col-md-12 pr-0">
                            <select class="form-control" v-model="selectedSKU" :disabled="!(selectedProductType && (selectedChange == '1' || selectedChange == '3'))">
                                <option disabled selected value="0">-select-</option>
                                <option v-for="item in SKUList"
                                        :value="{ id: item.productId, text: item.productName }"
                                        :key="item.productId"
                                    >
                                        {{ item.productName }}
                                </option>
                            </select>
                        </div>
                    </div>
                    <div class="col-md-4">
                        <p class="mt-1 col-md-12">Product Name</p>
                        <div class="col-md-12 pr-0">
                            <select class="form-control" v-model="selectedProduct" :disabled="!(selectedProductType && (selectedChange == '2' || selectedChange == '3'))">
                                <option disabled selected value="0">-select-</option>
                                <option v-for="item in ProductList"
                                        :value="{ id: item.productId, text: item.productName }"
                                        :key="item.productId"
                                    >
                                        {{ item.productName }}
                                </option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            <div class="mt-1 mb-2">
                <b-button v-b-modal.modal-sm
                    class="float-right mt-2 mb-3 p-2 pl-5 pr-5"
                    variant="primary"
                    :disabled="!comments"
                    @click="postReelChange"
                >
                    SUBMIT
                </b-button>
                <b-button v-b-modal.modal-sm
                    class="float-right mt-2 mb-3 mr-2 p-2 pl-5 pr-5"
                    variant="outline-primary"
                    @click="redirectToHome"
                >
                    CANCEL
                </b-button>
            </div>
            <!-- END: Card DATA-->
        </div>
    </main>
</template>

<script>
import axios from "axios";
import toast from '@/toast';
export default {

    data: () => ({
        orderNumber: "",
        orderStatus: "",
        warehouseName: "",
        paymentStatus: "",
        trackingID: "",
        editFlow: false,
        fulfillmentBool: false,
        showCartonSelection: false,
        selectedProductType: null,
        selectedProduct: {},
        selectedSKU: {},
        selectedPackage: {},
        selectedChange: "",
        packageList: [],
        productTypeList: [],
        ProductList: [],
        SKUList: [],
        packageObj: {},
        selectedFacilityLocation: "",
        expectedDate: null,
        comments: "",
        codeValue: "",
        selectAll: false,
        indeterminate: false,
        selectedCodes: [],
        codes: [],
        selectedRowIndex: 0,
        selectedCarton: {},
        facilityLocationList: [],
        fields: ['product', 'product_name', 'sku',"coil_size","no_of_coils","total_stamps",'actions'],
        items: {
            CartonCoilObject: {},
            coilListing: []
        },
        coilListing: []
    }),
    mounted() {
        this.getWarehouses();
        this.getPackageTypes();
        this.getProducts();
    },
    computed: {
    },
    methods: {
        postReelChange() {
            let reelChangeRequestReels = [];
            this.items.coilListing.map((item) => {
                var order = {
                    reelId: item.id,
                }
                reelChangeRequestReels.push(order);
            });
            let reqbody = {
                reelChangeType: parseInt(this.selectedChange),
                reelChangeProductId: parseInt(this.selectedProductType.id),
                changeToProductId: this.selectedProduct ? parseInt(this.selectedProduct.id) : null,
                changeToSkuId: this.selectedSKU ? parseInt(this.selectedSKU.id) : null,
                comments: this.comments,
                reelChangeRequestReels: reelChangeRequestReels
            }
            axios.post(process.env.VUE_APP_OrderMgmtApisBaseUrl +"/ReelChange",reqbody).then(() => {
                this.$router.push({ name: 'ChangeSKUProductNamelisting' });
            });
        },
        fetchCoils() {
            let tempCoilListing = [...this.items.coilListing];
            let obj = tempCoilListing.find(o => o.code == this.codeValue);
            if(obj === undefined) {
                let coilArray = [];
                if(this.selectedPackage.text == "Carton") {
                    axios.get("/ReelChange/GetReelProducts", {
                        baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                        params: {cartonCode:this.codeValue,productId:this.selectedProductType.id},
                    }).then((response) => {
                        if(response.data.length > 0) {
                            this.showCartonSelection = true;
                            this.selectedCarton = response.data;
                            response.data.map((reel)=>{
                                if(tempCoilListing.find(o => o.code == reel.code)  === undefined)
                                    this.codes.push(reel.code);
                                else {
                                    let msg = "Reel "+reel.code+" in this Carton is already added in the table";
                                    toast.warning(msg);
                                }
                            });
                        }
                    });
                    this.selectAll = false;
                } else {
                    this.showCartonSelection = false;
                    axios.get("/ReelChange/GetReelProduct", {
                        baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                        params: {reelCode:this.codeValue,productId:this.selectedProductType.id}
                    }).then((response) => {
                        let coil = {
                            code: response.data.code,
                            id: response.data.id,
                            productName: response.data.productName,
                            productType: response.data.productType,
                            sku: response.data.sku
                        }
                        coilArray.push(coil);
                        this.addCoil(coilArray);
                    });
                }
            } else {
                let msg = this.selectedPackage.text+" "+this.codeValue+" is already added in the table";
                toast.warning(msg);
                this.selectedPackage = "";
                this.clearCartonSelection();
            }
        },
        addCoil(coilArray) {
            let tempCoilListing = [...this.items.coilListing];
            if(!(coilArray[0].cartonId in this.items.CartonCoilObject)) {
                this.items.CartonCoilObject[coilArray[0].cartonId] = [];
            }
            if(this.selectedPackage.text == "Reel") {
                let tempCoil = {
                    code: coilArray[0].code,
                    id: coilArray[0].id,
                    productName: coilArray[0].productName,
                    productType: coilArray[0].productType,
                    sku: coilArray[0].sku
                }
                tempCoilListing.push(tempCoil);
                this.items.CartonCoilObject[coilArray[0].cartonId].push(coilArray[0].code);
            } else {
                coilArray.map((coil) => {
                    let tempCoil = {
                        code: coil.code,
                        id: coil.id,
                        productName: coil.productName,
                        productType: coil.productType,
                        sku: coil.sku
                    }
                    tempCoilListing.push(tempCoil);
                    this.items.CartonCoilObject[coilArray[0].cartonId].push(coil.code);
                });
            }
            this.items.coilListing = [...tempCoilListing];
            this.selectedPackage = "";
            this.clearCartonSelection();
        },
        onProductChange() {
            this.clearCartonSelection();
            this.getBrandProducts(this.selectedProductType.id);
            this.getSkus(this.selectedProductType.id);
        },
        onPackageChange() {
            this.clearCartonSelection();
        },
        toggleAll(checked) {
            this.selectedCodes = checked ? this.codes.slice() : []
        },
        cartonSelection() {
            let coilArray = [];
            this.selectedCodes.map((code) => {
                this.codeValue = code;
                this.selectedCarton.map((reel)=>{
                    if(this.codeValue == reel.code) {
                        let coil = {
                            code: reel.code,
                            id: reel.id,
                            productName: reel.productName,
                            productType: reel.productType,
                            sku: reel.sku
                        }
                        coilArray.push(coil);
                    }
                });
            });
            this.addCoil(coilArray);
            this.selectedPackage = "";
            this.clearCartonSelection();
        },
        resetCoils() {
            this.clearCartonSelection();
            this.items.coilListing = [];
            this.selectedPackage = "";
            this.selectedProductType = "";
        },
        clearCartonSelection() {
            this.codeValue = "";
            this.selectAll = false;
            this.indeterminate = false;
            this.selectedCodes = [];
            this.codes = [];
            this.showCartonSelection = false;
        },
        removeItem(rowIndex) {
            var coilCode = this.items.coilListing[rowIndex].code;
            if(this.packageObj[this.items.coilListing[rowIndex].packageType] == "Reel") {
                for (var key in this.items.CartonCoilObject) {
                    if (this.items.CartonCoilObject[key].includes(coilCode)) {
                        this.items.CartonCoilObject[key].splice(this.items.CartonCoilObject[key].indexOf(coilCode),1);
                        if(this.items.CartonCoilObject[key].length == 0) {
                            delete this.items.CartonCoilObject[key];
                        }
                    }
                }
            } else if (this.packageObj[this.items.coilListing[rowIndex].packageType] == "Carton") {
                delete this.items.CartonCoilObject[this.items.coilListing[rowIndex].packageId];
            }
            this.items.coilListing.splice(rowIndex,1);
        },
        typeChange(val) {
            if(val == 2) this.selectedSKU = "";
            else if(val == 1) this.selectedProduct = "";
        },
        redirectToHome() {
            this.$router.push({ name: 'ChangeSKUProductNamelisting' });
        },
        getWarehouses() {
            axios.get("/Meta/GetShippers", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
            }).then((response) => {
                var tempWarehouses = [];
                for (let key in response.data) {
                    tempWarehouses.push({'productId': key, 'productName': response.data[key]});
                }
                this.facilityLocationList = tempWarehouses;
            });
        },
        getPackageTypes() {
            axios.get("/Packages/GetPackageTypes", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
            }).then((response) => {
                var packageTypes = [];
                for (let key in response.data) {
                    if(response.data[key] !== "Pallet")
                        packageTypes.push({'productId': key, 'productName': response.data[key]});
                }
                this.packageList = packageTypes;
                this.packageObj = response.data;
            });
        },
        getProducts() {
            axios.get("/Meta/GetProducts", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
            }).then((response) => {
                var tempProductType = [];
                for (let key in response.data) {
                    tempProductType.push({ 'productId': key, 'productName': response.data[key] });
                }
                this.productTypeList = tempProductType;
            });
        },
        getBrandProducts(selectedProductId) {
            axios.get("/Meta/GetBrandProducts", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                params: { productId: selectedProductId }
            }).then((response) => {
                var tempProductName = [];
                for (let key in response.data) {
                    tempProductName.push({ 'productId': key, 'productName': response.data[key] });
                }
                this.ProductList = tempProductName;
            });
        },
        getSkus(selectedProductId) {
            axios.get("/Meta/GetSkus", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                params: { productId: selectedProductId }
            }).then((response) => {
                var tempSKU = [];
                for (let key in response.data) {
                    tempSKU.push({ 'productId': key, 'productName': response.data[key] });
                }
                this.SKUList = tempSKU;
            });
        },
    },
    watch: {
      selectedCodes(newVal) {
        // Handle changes in individual flavour checkboxes
        if (newVal.length === 0) {
          this.indeterminate = false
          this.selectAll = false
        } else if (newVal.length === this.codes.length) {
          this.indeterminate = false
          this.selectAll = true
        } else {
          this.indeterminate = true
          this.selectAll = false
        }
      }
    }

};
</script>